* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

html {
  font-size: 62.5%; }

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: 'Montserrat', sans-serif;
  position: relative;
  z-index: -999;
  background: black;
  background: -webkit-linear-gradient(270deg, black 0%, #3f3f3f 71%, #505050 100%);
  background: -webkit-gradient(linear, right top, left top, from(black), color-stop(71%, #3f3f3f), to(#505050));
  background: -webkit-linear-gradient(right, black 0%, #3f3f3f 71%, #505050 100%);
  background: linear-gradient(270deg, black 0%, #3f3f3f 71%, #505050 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#000000", endColorstr="#505050", GradientType=1);
  color: #fff; }

#root {
  position: relative;
  z-index: -999; }

a {
  text-decoration: none;
  color: white; }

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
textarea:-webkit-autofill,
textarea:-webkit-autofill:hover,
textarea:-webkit-autofill:focus,
select:-webkit-autofill,
select:-webkit-autofill:hover,
select:-webkit-autofill:focus {
  -webkit-text-fill-color: white;
  box-shadow: 0 0 0px 1000px none inset;
  -webkit-transition: background-color 5000s ease-in-out 0s;
  transition: background-color 5000s ease-in-out 0s; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

.production-card {
  cursor: pointer;
  color: #fff;
  min-width: 30rem;
  height: 25rem;
  margin: 1.5rem;
  border: 1px solid white;
  display: flex;
  position: relative;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  z-index: 15;
  overflow: hidden; }
  .production-card .production-image {
    width: 100%;
    height: 100%;
    background-position: center;
    background-size: cover; }
  .production-card .hover-background {
    position: absolute;
    width: 100%;
    height: 100%; }
  .production-card .card-title {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: bold;
    font-size: 2.5rem;
    margin: 0; }
  .production-card .card-content {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: #fff;
    text-align: center;
    z-index: 20;
    background-color: #1f1a18; }
    .production-card .card-content .card-text {
      align-self: center;
      font-weight: lighter;
      font-size: 1.5rem;
      letter-spacing: 0.75px;
      margin: 0 1.3rem;
      padding: 0 7px; }

.show {
  opacity: 1; }

.production-list {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  z-index: 2; }

@media all and (max-width: 680px) {
  .production-list {
    flex-direction: column; } }

.video-component {
  position: relative;
  justify-content: center;
  width: 100%; }
  .video-component .player-wrapper {
    position: relative;
    padding-top: 56.25%;
    /* Player ratio: 100 / (1280 / 720) */ }
    .video-component .player-wrapper .react-player {
      position: absolute;
      top: 0;
      left: 0; }

.card-container-background {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 2;
  display: flex;
  justify-content: center;
  align-items: center; }

.card-container {
  max-width: 50rem;
  position: absolute;
  background-color: #1f1a18;
  border: 1px solid #fff;
  z-index: 5;
  color: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 5rem 2.5rem; }
  .card-container .close-btn {
    font-size: 1.3rem;
    position: absolute;
    right: 2.5rem;
    top: 1.5rem;
    cursor: pointer;
    border-bottom: 1px solid #fff; }
    .card-container .close-btn:hover {
      color: gray;
      border-color: gray; }
  .card-container .title {
    text-align: center;
    font-size: 2.2rem;
    font-weight: lighter;
    letter-spacing: 1.7px;
    display: inline-block;
    margin-bottom: 2rem; }
  .card-container .production {
    display: flex;
    flex-direction: column;
    align-items: center; }
    .card-container .production .desc {
      display: flex;
      background-color: #2c2523;
      border-radius: 1.5rem;
      padding: 0.5rem 1.5rem;
      text-align: center;
      margin-bottom: 1.5rem;
      letter-spacing: 0.7px;
      font-size: 15px;
      line-height: 1.5; }
    .card-container .production .btn {
      cursor: pointer;
      width: 15rem;
      margin-bottom: 2rem;
      padding: 1.5rem 0;
      border: 1px solid white;
      background-color: #1f1a18;
      color: white;
      display: flex;
      justify-content: center;
      font-size: 1.3rem;
      letter-spacing: 0.7px; }
      .card-container .production .btn:hover {
        background-color: #423937;
        -webkit-transform: translateY(-3px);
                transform: translateY(-3px);
        box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.2); }
      .card-container .production .btn:active {
        -webkit-transform: translateY(-1px);
                transform: translateY(-1px);
        box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.2); }
    .card-container .production .sample {
      width: 100%; }

.homepage {
  width: 100%;
  min-height: 100vh;
  height: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  background-size: cover;
  background-position: center;
  z-index: -1; }
  .homepage .log-off-btn {
    position: fixed;
    color: #fff;
    z-index: 999;
    top: 1.7rem;
    right: 3.5rem;
    padding: 0.7rem 1.3rem;
    background-color: #000; }

.homepage::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.35); }

.group {
  position: relative;
  margin: 45px 0; }
  .group .form-input {
    background: none;
    color: #8f8f8f;
    font-size: 18px;
    padding: 10px 10px 10px 5px;
    display: block;
    width: 100%;
    border: none;
    border-radius: 0;
    border-bottom: 1px solid #8f8f8f;
    margin: 25px 0; }
    .group .form-input:focus {
      outline: none; }
    .group .form-input:focus ~ .form-input-label {
      top: -14px;
      font-size: 12px;
      color: white; }
  .group input[type='password'] {
    letter-spacing: 0.3em; }
  .group .form-input-label {
    color: #8f8f8f;
    font-size: 16px;
    font-weight: normal;
    position: absolute;
    pointer-events: none;
    left: 5px;
    top: 10px;
    -webkit-transition: 300ms ease all;
    transition: 300ms ease all; }
    .group .form-input-label.shrink {
      top: -14px;
      font-size: 12px;
      color: white; }

.custom-button {
  min-width: 16.5rem;
  width: auto;
  height: 5rem;
  letter-spacing: 0.5px;
  line-height: 5rem;
  padding: 0 3.5rem 0 3.5rem;
  font-size: 1.5rem;
  background-color: black;
  color: white;
  text-transform: uppercase;
  font-family: 'Open Sans Condensed';
  font-weight: bolder;
  border: none;
  cursor: pointer;
  display: flex;
  justify-content: center; }
  .custom-button:hover {
    background-color: white;
    color: black;
    border: 1px solid black; }
  .custom-button.google-sign-in {
    background-color: #4285f4;
    color: #fff; }
    .custom-button.google-sign-in:hover {
      background-color: #357ae8;
      border: none; }
  .custom-button.inverted {
    background-color: white;
    color: black;
    border: 1px solid black; }
    .custom-button.inverted:hover {
      background-color: black;
      color: white;
      border: none; }

.sign-in {
  width: 38rem;
  display: flex;
  flex-direction: column;
  margin: 1rem 0; }
  .sign-in .title {
    font-size: 2.2rem;
    margin: 1rem 0; }
    .sign-in .title-sec {
      font-size: 1.5rem; }
  .sign-in .buttons {
    display: flex;
    justify-content: space-between; }

.sign-in-and-sign-out {
  display: flex;
  width: 850px;
  justify-content: space-between;
  margin: 30px auto;
  color: white;
  padding: 25px 35px; }

.todo-item {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  border-bottom: 1px solid white; }
  .todo-item .todo-time {
    width: 50px;
    display: flex;
    justify-content: center;
    padding: 10px 9px;
    border-right: 1px solid white; }
  .todo-item .todo-text {
    display: flex;
    word-break: break-all;
    padding: 10px 9px;
    width: 180px; }
  .todo-item .todo-delete {
    background-color: #d91e18;
    padding: 10px 10px; }
  .todo-item:hover .todo-delete {
    cursor: pointer; }

.todo-input {
  width: 100%;
  display: flex;
  flex-direction: reverse;
  justify-content: center; }
  .todo-input .input {
    background-color: rgba(0, 0, 0, 0.5);
    border: none;
    padding: 1rem;
    width: 100%;
    color: #fff;
    outline: none; }
  .todo-input .input-time {
    background-color: rgba(0, 0, 0, 0.5);
    padding: 1rem;
    width: 35px;
    color: #fff;
    outline: none;
    text-align: center; }
  .todo-input .btn-submit {
    background-color: rgba(0, 0, 0, 0.2);
    width: 55px;
    color: #fff;
    outline: none;
    text-align: center; }

.video-section {
  overflow-y: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  bottom: 0;
  top: 0;
  left: 0;
  right: 0;
  background: black;
  background: -webkit-linear-gradient(270deg, black 0%, #3f3f3f 71%, #505050 100%);
  background: -webkit-gradient(linear, right top, left top, from(black), color-stop(71%, #3f3f3f), to(#505050));
  background: -webkit-linear-gradient(right, black 0%, #3f3f3f 71%, #505050 100%);
  background: linear-gradient(270deg, black 0%, #3f3f3f 71%, #505050 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#000000",endColorstr="#505050",GradientType=1);
  color: #fff; }
  .video-section .container {
    position: relative;
    width: 95%;
    height: 95%;
    display: flex;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.5);
    box-shadow: 0px 0px 12px 0 rgba(255, 255, 255, 0.8); }
  .video-section .todolist {
    height: 100%;
    overflow-y: scroll;
    border: 1px solid white; }
  .video-section .list {
